:root {
  --font-size-base: 62.5%;
  --font-mono: "JetBrains Mono", monospace;
  --font-sans: "Space Grotesk", system-ui, sans-serif;
  --color-bg: #0b0f12;
  --color-bg-secondary: #0f1417;
  --color-bg-gradient-start: #0c1116;
  --color-bg-gradient-end: #080b0e;
  --color-text: #dbe7ef;
  --color-text-muted: #9aa6b2;
  --color-accent: #58a6ff;
  --color-accent-rgb: 88 166 255;
  --color-border: #ffffff0f;
  --color-red: #ff6b6b;
  --color-yellow: #fc6;
  --color-green: #2dd4bf;
  --spacing-xs: .3rem;
  --spacing-sm: .6rem;
  --spacing-md: .8rem;
  --spacing-lg: 1.2rem;
  --spacing-xl: 1.6rem;
  --spacing-2xl: 2rem;
  --spacing-3xl: 2.4rem;
  --spacing-4xl: 3.2rem;
  --radius-sm: .6rem;
  --radius-md: .8rem;
  --radius-lg: 1.2rem;
  --radius-xl: 1.6rem;
  --z-base: 1;
  --z-nav: 10;
  --z-overlay: 20;
  --z-toast: 30;
  --z-cursor: 9999;
  --transition-fast: .12s ease-out;
  --transition-base: .2s ease;
  --transition-slow: .35s ease;
  --sidebar-width: 28rem;
  --mobile-breakpoint: 768px;
  --desktop-breakpoint: 800px;
  --ultrawide-breakpoint: 1800px;
}

* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  font-size: var(--font-size-base);
}

html, body {
  inline-size: 100%;
  max-inline-size: 100%;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

body {
  background: #0b0f12;
  background: var(--color-bg);
  color: var(--color-text);
  font-family: Space Grotesk, system-ui, sans-serif;
  font-family: var(--font-sans);
  font-size: 1rem;
  line-height: 1.5;
}

body, a, button, input, textarea {
  cursor: none !important;
}

.custom-cursor {
  background: #58a6ff;
  background: var(--color-accent);
  block-size: 14px;
  inline-size: 14px;
  box-shadow: 0 0 0 6px #58a6ff1f;
  box-shadow: 0 0 0 6px rgb(var(--color-accent-rgb) / .12);
  pointer-events: none;
  z-index: 9999;
  z-index: var(--z-cursor);
  opacity: .85;
  transition: transform .12s ease-out, box-shadow .2s, opacity .2s;
  transition: transform var(--transition-fast), box-shadow var(--transition-base), opacity var(--transition-base);
  border-radius: 50%;
  position: fixed;
  transform: translate(-50%, -50%);
}

.custom-cursor.hover {
  block-size: 28px;
  inline-size: 28px;
  box-shadow: 0 0 0 1px #58a6ff, 0 0 0 6px #58a6ff26;
  box-shadow: 0 0 0 1px var(--color-accent), 0 0 0 6px rgb(var(--color-accent-rgb) / .15);
  background: none;
}

.custom-cursor.hide {
  opacity: 0;
  transform: translate(-50%, -50%)scale(.7);
}

:focus-visible {
  outline: .2rem solid #58a6ff;
  outline: .2rem solid var(--color-accent);
  outline-offset: .2rem;
}

.app {
  grid-template-columns: 28rem 1fr;
  grid-template-columns: var(--sidebar-width) 1fr;
  min-block-size: 100vh;
  display: grid;
}

.sidebar {
  background: linear-gradient(#0c1116, #080b0e);
  background: linear-gradient(180deg, var(--color-bg-gradient-start), var(--color-bg-gradient-end));
  border-inline-end: .1rem solid #ffffff0f;
  border-inline-end: .1rem solid var(--color-border);
  padding: 2rem;
  padding: var(--spacing-2xl);
  gap: 2.4rem;
  gap: var(--spacing-3xl);
  flex-direction: column;
  display: flex;
}

.brand {
  gap: 1.2rem;
  gap: var(--spacing-lg);
  align-items: center;
  display: flex;
}

.avatar {
  border-radius: .8rem;
  border-radius: var(--radius-md);
  block-size: 5.6rem;
  inline-size: 5.6rem;
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-accent);
  background: #111820;
  place-items: center;
  font-size: 2.2rem;
  display: grid;
  overflow: hidden;
}

.avatar img {
  object-fit: cover;
  border-radius: inherit;
  block-size: 100%;
  inline-size: 100%;
  display: block;
}

.title {
  font-size: 1.7rem;
  font-weight: 700;
}

.subtitle {
  font-family: JetBrains Mono, monospace;
  font-size: 1.2rem;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
}

.nav {
  gap: .6rem;
  gap: var(--spacing-sm);
  flex-direction: column;
  display: flex;
}

.nav button {
  align-items: center;
  gap: .8rem;
  gap: var(--spacing-md);
  border: .1rem solid #ffffff0f;
  border: .1rem solid var(--color-border);
  padding: .8rem 1.2rem;
  padding: var(--spacing-md) var(--spacing-lg);
  border-radius: .8rem;
  border-radius: var(--radius-md);
  color: var(--color-text-muted);
  letter-spacing: .05em;
  cursor: pointer;
  transition: color .2s, border-color .2s, background-color .2s;
  transition: color var(--transition-base), border-color var(--transition-base), background-color var(--transition-base);
  background: none;
  font-size: 1.3rem;
  font-weight: 600;
  display: flex;
}

:is(.nav button):hover {
  background-color: #ffffff08;
}

:is(.nav button) svg {
  stroke: currentColor;
  opacity: .5;
  block-size: 1.6rem;
  inline-size: 1.6rem;
  transition: opacity .2s;
  transition: opacity var(--transition-base);
}

.active:is(.nav button) {
  color: var(--color-accent);
  border-color: #58a6ff;
  border-color: var(--color-accent);
  background-color: #58a6ff14;
  background-color: rgb(var(--color-accent-rgb) / .08);
  letter-spacing: .1em;
  font-weight: 700;
}

.active:is(.nav button) svg {
  opacity: 1;
}

.footer {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  opacity: .8;
  padding: 2rem;
  padding: var(--spacing-2xl);
  font-size: 1.1rem;
}

@media (width >= 800px) {
  .footer {
    inline-size: 28rem;
    inline-size: var(--sidebar-width);
    border-inline-end: .1rem solid #ffffff0f;
    border-inline-end: .1rem solid var(--color-border);
    background: linear-gradient(#0c1116, #080b0e);
    background: linear-gradient(180deg, var(--color-bg-gradient-start), var(--color-bg-gradient-end));
    position: fixed;
    inset-block-end: 0;
    inset-inline-start: 0;
  }
}

@media (width <= 768px) {
  .footer {
    border-block-start: .1rem solid #ffffff0f;
    border-block-start: .1rem solid var(--color-border);
    text-align: center;
    inline-size: 100%;
    margin-block-start: 3.2rem;
    margin-block-start: var(--spacing-4xl);
    position: static;
  }
}

.main {
  background: #0b0f12;
  background: var(--color-bg);
  overflow: hidden;
}

.islands {
  block-size: 100%;
  position: relative;
}

.island {
  padding: 3.2rem;
  padding: var(--spacing-4xl);
  opacity: 0;
  pointer-events: none;
  position: absolute;
  inset: 0;
  overflow-y: auto;
  transform: translateY(1rem);
}

.island.active {
  opacity: 1;
  pointer-events: auto;
  transform: none;
}

.card {
  border: .1rem solid #ffffff0f;
  border: .1rem solid var(--color-border);
  border-radius: 1.2rem;
  border-radius: var(--radius-lg);
  padding: 2rem;
  padding: var(--spacing-2xl);
  margin-block-end: 3.2rem;
  background: linear-gradient(#ffffff08, #0000);
  margin-block-end: var(--spacing-4xl);
}

.card h4 {
  margin: 0 0 .6rem;
  margin: 0 0 var(--spacing-sm) 0;
  font-size: 1.6rem;
  font-weight: 600;
  line-height: 1.3;
}

.section-title {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  margin-block-end: 1.6rem;
  margin-block-end: var(--spacing-xl);
  font-size: 1.5rem;
}

.card.terminal {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  font-size: 1.4rem;
  position: relative;
}

.term-head {
  block-size: 2.4rem;
  block-size: var(--spacing-3xl);
  margin-block-end: 1.2rem;
  margin-block-end: var(--spacing-lg);
  position: relative;
}

.term-dots {
  gap: .6rem;
  gap: var(--spacing-sm);
  display: flex;
  position: absolute;
  inset-block-start: 0;
  inset-inline-start: 0;
}

.term-dots .dot {
  border-radius: 50%;
  block-size: 1rem;
  inline-size: 1rem;
}

.red:is(.term-dots .dot) {
  background: #ff6b6b;
  background: var(--color-red);
}

.yellow:is(.term-dots .dot) {
  background: #fc6;
  background: var(--color-yellow);
}

.green:is(.term-dots .dot) {
  background: #2dd4bf;
  background: var(--color-green);
}

.term-replay {
  align-items: center;
  gap: .6rem;
  gap: var(--spacing-sm);
  border: .1rem dashed #ffffff0f;
  border: .1rem dashed var(--color-border);
  border-radius: .6rem;
  border-radius: var(--radius-sm);
  padding: .3rem .6rem;
  padding: var(--spacing-xs) var(--spacing-sm);
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  z-index: 5;
  transition: color .2s, border-color .2s, transform .15s;
  transition: color var(--transition-base), border-color var(--transition-base), transform .15s ease;
  background: none;
  font-size: 1.2rem;
  display: inline-flex;
  position: absolute;
  inset-block-start: 0;
  inset-inline-end: 0;
}

.term-replay svg {
  block-size: 1.4rem;
  inline-size: 1.4rem;
}

.term-replay:hover, .term-replay:focus-visible {
  color: var(--color-accent);
  border-color: #58a6ff;
  border-color: var(--color-accent);
  transform: translateY(-.1rem);
}

.term-lines {
  white-space: pre-wrap;
  color: var(--color-text-muted);
}

@media (width <= 768px) {
  .term-replay .replay-text {
    display: none;
  }

  .term-replay {
    padding: .35rem;
  }
}

.badges {
  gap: .6rem;
  gap: var(--spacing-sm);
  flex-wrap: wrap;
  display: flex;
}

.badge {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  padding: .3rem .8rem;
  padding: var(--spacing-xs) var(--spacing-md);
  border-radius: .6rem;
  border-radius: var(--radius-sm);
  border: .1rem solid #ffffff0f;
  border: .1rem solid var(--color-border);
  font-size: 1.2rem;
}

.skill-group {
  margin-block-end: 2rem;
  margin-block-end: var(--spacing-2xl);
}

.skill-group:last-child {
  margin-block-end: 0;
}

.skill-label {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  margin-block-end: .6rem;
  margin-block-end: var(--spacing-sm);
  font-size: 1.2rem;
}

.projects {
  gap: 1.6rem;
  gap: var(--spacing-xl);
  grid-template-columns: repeat(auto-fit, minmax(24rem, 1fr));
  display: grid;
}

.project-card {
  padding-block-end: 4.4rem;
  position: relative;
}

.see-more-btn {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  align-items: center;
  gap: .3rem;
  gap: var(--spacing-xs);
  padding: .3rem .8rem;
  padding: var(--spacing-xs) var(--spacing-md);
  border-radius: .6rem;
  border-radius: var(--radius-sm);
  border: .1rem dashed #ffffff0f;
  border: .1rem dashed var(--color-border);
  cursor: pointer;
  transition: color .2s, border-color .2s, transform .15s;
  transition: color var(--transition-base), border-color var(--transition-base), transform .15s ease;
  background: none;
  font-size: 1.2rem;
  text-decoration: none;
  display: inline-flex;
}

.see-more-btn:hover, .see-more-btn:focus-visible {
  color: var(--color-accent);
  border-color: #58a6ff;
  border-color: var(--color-accent);
  transform: translateY(-.1rem);
}

.project-card .see-more-btn {
  inset-block-end: 1.6rem;
  inset-inline-end: 1.6rem;
  font-size: 1.1rem;
  position: absolute;
  inset-block-end: var(--spacing-xl);
  inset-inline-end: var(--spacing-xl);
}

label {
  font-family: JetBrains Mono, monospace;
  font-family: var(--font-mono);
  color: var(--color-text-muted);
  margin-block-start: 1.6rem;
  margin-block-start: var(--spacing-xl);
  font-size: 1.2rem;
  display: block;
}

input, textarea {
  inline-size: 100%;
  padding: .8rem;
  padding: var(--spacing-md);
  border: .1rem solid #ffffff0f;
  border: .1rem solid var(--color-border);
  border-radius: .6rem;
  border-radius: var(--radius-sm);
  color: var(--color-text);
  margin-block-start: .3rem;
  background: none;
  margin-block-start: var(--spacing-xs);
  font-family: inherit;
  caret-color: #0000 !important;
}

:is(input, textarea):focus {
  cursor: none !important;
}

textarea {
  resize: none;
  min-block-size: 6.4rem;
  overflow: hidden;
}

button.cta {
  padding: .8rem 1.2rem;
  padding: var(--spacing-md) var(--spacing-lg);
  border: .1rem solid #58a6ff;
  border: .1rem solid var(--color-accent);
  border-radius: .8rem;
  border-radius: var(--radius-md);
  color: var(--color-accent);
  cursor: pointer;
  margin-block-start: 2rem;
  background: none;
  margin-block-start: var(--spacing-2xl);
}

@media (width <= 768px) {
  .app {
    grid-template-columns: 1fr;
  }

  body {
    overflow: auto;
  }

  .main {
    padding-block-start: 14rem;
    overflow: visible;
  }

  .islands, .island {
    opacity: 1;
    pointer-events: auto;
    block-size: auto;
    padding: 2rem;
    padding: var(--spacing-2xl);
    scroll-margin-block-start: 14rem;
    position: static;
    transform: none;
  }

  .nav {
    gap: .6rem;
    gap: var(--spacing-sm);
    flex-flow: wrap;
  }

  .nav button {
    padding: .3rem .8rem;
    padding: var(--spacing-xs) var(--spacing-md);
    letter-spacing: .02em;
    font-size: 1.2rem;
  }

  .active:is(.nav button) {
    letter-spacing: .04em;
  }

  .sidebar {
    z-index: 10;
    z-index: var(--z-nav);
    position: fixed;
    inset-block-start: 0;
    inset-inline: 0;
  }
}

@media (width >= 800px) {
  html, body {
    block-size: 100%;
  }

  body {
    overflow: hidden;
  }

  .app {
    block-size: 100vh;
  }

  .sidebar, .main, .islands, .island {
    block-size: 100%;
  }

  @media (prefers-reduced-motion: no-preference) {
    .island {
      transition: opacity .35s, transform .35s;
      transition: opacity var(--transition-slow), transform var(--transition-slow);
    }
  }
}

@media (width >= 1800px) {
  body {
    background: linear-gradient(90deg, #0b0f12fb, #0b0f12, #0b0f12fb);
    background: linear-gradient(90deg, #0b0f12fb, var(--color-bg), #0b0f12fb);
    padding-block: 25vh;
    padding-inline: 25vw;
  }

  .app {
    background: #0b0f12;
    background: var(--color-bg);
    border-radius: 1.6rem;
    border-radius: var(--radius-xl);
    block-size: 50vh;
    min-block-size: 0;
    inline-size: 100%;
    overflow: hidden;
    box-shadow: 0 20px 60px #00000073, 0 0 0 1px #ffffff0a;
  }

  .island {
    max-inline-size: 190rem;
    margin-inline: auto;
  }

  .card.terminal {
    max-inline-size: 150rem;
    margin-inline: auto;
  }

  .projects {
    grid-template-columns: repeat(4, minmax(28rem, 1fr));
  }

  .footer {
    background: none;
    border: none;
    position: static;
  }
}
